import { Editor } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { buildBead } from "../editorObjects/Bead";
import { buildLine } from "../editorObjects/Line";
import { BeadSnapperImmerReducer } from "./BeadSnapperImmerReducer";

export class PersistImmerReducer extends BeadSnapperImmerReducer {
  action_onClearCanvas() {
    this.draftState.appData.beads = {};
    this.draftState.appData.lines = {};
    this.draftState.ui.selectedBeadsIds = {};
    this.draftState.ui.selectedBeadsIdsCount = 0;
    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIdsCount = 0;
    this.draftState.ui.saveInfo.saved = false;
    this.draftState.ui.saveInfo.haveUnsavedChanges = false;
  }

  action_readSavedCanvas() {
    console.log("action_readSavedCanvas");
    if ("localStorage" in window) {
      this.draftState.ui.saveInfo.saved = true;
      this.draftState.ui.saveInfo.haveUnsavedChanges = false;
      const readedData = localStorage.getItem("appData");
      if (readedData !== null) {
        try {
          const cached = JSON.parse(readedData) as Editor["appData"];
          this.draftState.appData = {
            beads: {},
            lines: {},
          };
          Object.keys(cached.beads).forEach(beadId => {
            const currBead = cached.beads[beadId];
            const bead = buildBead(currBead);
            this.draftState.appData.beads[beadId] = bead;
          });
          Object.keys(cached.lines).forEach(lineId => {
            const currLine = cached.lines[lineId];
            const line = buildLine(currLine);
            this.draftState.appData.lines[lineId] = line;
          });
          Object.keys(this.draftState.appData.lines).forEach(lineId => {
            this.updateBeadsPositions(lineId);
          });
          console.log("action_readSavedCanvas loaded");
        } catch (err) {
          console.error("action_readSavedCanvas error: ", err);
        }
      } else {
        console.info("No data in localstorage");
      }
    } else {
      console.info("No localstorage available");
    }
  }

  action_onSaveCanvas() {
    if ("localStorage" in window) {
      localStorage.setItem("appData", JSON.stringify(this.draftState.appData));
      this.draftState.ui.saveInfo.saved = true;
      this.draftState.ui.saveInfo.haveUnsavedChanges = false;
      console.log("action_onSaveCanvas saved");
    } else {
      this.draftState.ui.saveInfo.saved = false;
      console.info("No localstorage available");
    }
  }
}

