import React from "react";
import { uiButton } from "../../colors";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import { FieldSlider } from "./FieldSlider";
import { ToolbarFieldSet } from "./ToolbarFieldSet";
import NumberFormat from "react-number-format";

export const FieldsetBeadRotate = ({ value, onChange }) => {
  return (
    <ToolbarFieldSet
      selected={`${value}deg`}
      label={"Rotate"}
      icon={<FontAwesomeIcon icon={faSyncAlt} color={uiButton} size={"lg"} />}
    >
      <React.Fragment>
        <FieldSlider
          value={value}
          onChange={value => {
            console.log("FieldSlider", value);
            onChange(value);
          }}
          max={180}
          min={-180}
          defaultValue={0}
          marks={{
            "-180": "-180",
            "0": "0",
            "180": "180",
          }}
        />
        <NumberFormat
          suffix={"deg"}
          allowNegative={true}
          allowEmptyFormatting={true}
          value={value}
          onValueChange={event => {
            onChange(event.floatValue);
          }}
          placeholder=" deg"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "1em",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "3px 10px",
            marginBottom: "15px",
            marginTop: "5px",
          }}
        />
      </React.Fragment>
    </ToolbarFieldSet>
  );
};
