import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faEdit,
  faArrowsAlt,
  faPaintBrush,
} from "@fortawesome/free-solid-svg-icons";
import { EditorMode } from "../../editorObjects/Editor";
import { uiButton } from "../colors";
import { useDispatch } from "react-redux";
import { Actions } from "../../store";

export const EditorModes = ({ editorMode }) => {
  const dispatch = useDispatch();

  const onChangeEditorMode = (newEditorMode: EditorMode) => {
    dispatch(Actions.ChangeEditorMode.action_onChangeEditorMode(newEditorMode));
  };

  return (
    <div className="">
      <StateButton
        label={"Idle"}
        icon={<FontAwesomeIcon icon={faArrowsAlt} color={uiButton} />}
        onClick={() => onChangeEditorMode(EditorMode.Idle)}
        isSelected={editorMode === EditorMode.Idle}
      />

      <StateButton
        label={"Edit Lines"}
        icon={<FontAwesomeIcon icon={faEdit} color={uiButton} />}
        onClick={() => onChangeEditorMode(EditorMode.EditLines)}
        isSelected={editorMode === EditorMode.EditLines}
      />

      <StateButton
        label={"Edit Beads"}
        icon={<FontAwesomeIcon icon={faEdit} color={uiButton} />}
        onClick={() => onChangeEditorMode(EditorMode.EditBeads)}
        isSelected={editorMode === EditorMode.EditBeads}
      />

      <StateButton
        label={"AddLine"}
        icon={<FontAwesomeIcon icon={faPlus} color={uiButton} />}
        onClick={() => onChangeEditorMode(EditorMode.AddLine)}
        isSelected={editorMode === EditorMode.AddLine}
      />

      <StateButton
        label={"Paint"}
        icon={<FontAwesomeIcon icon={faPaintBrush} color={uiButton} />}
        onClick={() => onChangeEditorMode(EditorMode.Paint)}
        isSelected={editorMode === EditorMode.Paint}
      />
    </div>
  );
};

const StateButton = ({ label, isSelected, onClick, icon }) => {
  return (
    <div
      className={`
              button 
              ${isSelected ? "is-active" : ""}
            `}
      onClick={onClick}
    >
      <span className="icon is-small">{icon}</span>
      <span>{label}</span>
    </div>
  );
};
