import React, { useState, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor, BrushType } from "../../editorObjects/Editor";
import { getTranslate3dCss } from "./MapValuesToCss";
import { Actions } from "../../store";

export const Brush = ({ isVisible }) => {
  if (isVisible === true) {
    return <Content />;
  }
  return <React.Fragment />;
};

const Content = () => {
  const brush = useSelector((state: Editor) => {
    return state.ui.brush;
  });
  const { type, color, size } = brush;
  if (type === BrushType.Color) {
    return <ColorBrush color={color} size={size} />;
  }
  return <React.Fragment />;
};

const ColorBrush = ({ color, size }) => {
  const dispatch = useDispatch();
  const [isVisible, set_isVisible] = useState(false);
  const [isActivated, set_isActivated] = useState(false);
  const [pointX, set_pointX] = useState(0);
  const [pointY, set_pointY] = useState(0);
  const [rect, set_rect] = useState<ClientRect | null>(null);
  const sceneWrapperRef = useRef<HTMLDivElement>(null);
  const onMouseEnter = (event: React.MouseEvent<HTMLDivElement>) => {
    set_isVisible(true);
  };
  const onMouseLeave = (event: React.MouseEvent<HTMLDivElement>) => {
    set_isVisible(false);
  };
  const onMouseMove = (event: React.MouseEvent<HTMLDivElement>) => {
    if (rect === null) {
      if (sceneWrapperRef.current) {
        set_rect(sceneWrapperRef.current.getBoundingClientRect());
      }
    }
    if (rect !== null) {
      const x = event.clientX - rect.left - size / 2;
      const y = event.clientY - rect.top - size / 2;
      set_pointX(x);
      set_pointY(y);
      if (isActivated) {
        dispatch(Actions.Paint.action_applyColorBrush(x, y));
      }
    }
  };
  const onMouseDown = (event: React.MouseEvent<HTMLDivElement>) => {
    set_isActivated(true);
    if (rect === null) {
      if (sceneWrapperRef.current) {
        const rect = sceneWrapperRef.current.getBoundingClientRect();
        set_rect(rect);
        const x = event.clientX - rect.left - size / 2;
        const y = event.clientY - rect.top - size / 2;
        set_pointX(x);
        set_pointY(y);
        dispatch(Actions.Paint.action_applyColorBrush(x, y));
      }
    }
    if (rect !== null) {
      const x = event.clientX - rect.left - size / 2;
      const y = event.clientY - rect.top - size / 2;
      set_pointX(x);
      set_pointY(y);
      dispatch(Actions.Paint.action_applyColorBrush(x, y));
    }
  };
  const onMouseUp = (event: React.MouseEvent<HTMLDivElement>) => {
    set_isActivated(false);
  };
  return (
    <React.Fragment>
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          backgroundColor: "transparent",
          zIndex: 20,
          cursor: "cell",
        }}
        ref={sceneWrapperRef}
        onMouseDown={onMouseDown}
        onMouseUp={onMouseUp}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        onMouseMove={onMouseMove}
      ></div>

      <BrushElement
        y={pointY}
        x={pointX}
        color={color}
        size={size}
        isVisible={isVisible}
      />
    </React.Fragment>
  );
};

const BrushElement = ({ x, y, color, size, isVisible }) => {
  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        transform: getTranslate3dCss(x, y),
        backgroundColor: color,
        width: size,
        height: size,
        border: "5px solid #aeaeae",
        borderRadius: "100%",
        opacity: isVisible === true ? 0.8 : 0,
        zIndex: 19,
      }}
    >
      &nbsp;
    </div>
  );
};
