import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Editor,
  EditorMenuMain,
  EditorGridType,
} from "../../editorObjects/Editor";
import { EditorChangeState } from "../Toolbar/EditorChangeState";
import { FieldCheckbox } from "../Toolbar/ui/FieldCheckbox";
import NumberFormat from "react-number-format";
import { Actions } from "../../store";
import { SliderWithTooltip } from "../Toolbar/ui/FieldSlider";

export const TopbarSecoundary = () => {
  const currentMenuMainOpen = useSelector((state: Editor) => {
    return state.ui.currentMenuMainOpen;
  });
  if (currentMenuMainOpen === EditorMenuMain.File) {
    return <SubMenuFile />;
  }
  if (currentMenuMainOpen === EditorMenuMain.Grid) {
    return <SubMenuGrid />;
  }
  if (currentMenuMainOpen === EditorMenuMain.Background) {
    return <SubMenuBackground />;
  }
  return <React.Fragment />;
};

const SubMenuFile = () => {
  const dispatch = useDispatch();
  return (
    <div>
      <EditorChangeState />
    </div>
  );
};

const SubMenuBackground = () => {
  const dispatch = useDispatch();
  const image = useSelector((state: Editor) => {
    return state.ui.background.image;
  });

  const { position, url, scale, opacity } = image;
  const onChange_clear = () => {
    dispatch(Actions.Topbar.action_background_image_clear());
  };
  const onChange_upload = event => {
    const selectedFiles: FileList = event.target.files;
    const files = Array.from(selectedFiles);
    if (files.length === 0) {
      console.error("no files selected");
      return;
    }
    let file = files[0];
    const types = ["image/png", "image/jpeg", "image/gif"];
    if (types.every(type => file.type !== type)) {
      console.error(`'${file.type}' is not a supported format`);
      return;
    }
    if (file.size > 1 * 1024 * 1024) {
      console.error(`'${file.name}' is too large, please pick a smaller file`);
      return;
    }
    let reader = new FileReader();
    reader.onloadend = () => {
      dispatch(Actions.Topbar.action_background_image_upload(reader.result));
    };
    reader.readAsDataURL(file);
    event.preventDefault();
  };
  const onChange_pointX = value => {
    dispatch(Actions.Topbar.action_background_image_pointX(value));
  };
  const onChange_pointY = value => {
    dispatch(Actions.Topbar.action_background_image_pointY(value));
  };
  const onChange_scale = value => {
    dispatch(Actions.Topbar.action_background_image_scale(value));
  };
  const onChange_opacity = value => {
    dispatch(Actions.Topbar.action_background_image_opacity(value));
  };
  return (
    <div
      style={{ flexWrap: "nowrap", display: "flex", alignItems: "flex-start" }}
    >
      <div style={{ flexWrap: "nowrap", display: "flex", marginTop: "4px" }}>
        <div className="control">
          <div className={` button is-light `} onClick={onChange_clear}>
            <span>Clear</span>
          </div>
        </div>

        <div className="control">
          <div className={` button is-light `} style={{ position: "relative" }}>
            <input
              type="file"
              onChange={onChange_upload}
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
                opacity: 0,
                zIndex: 2,
              }}
            />
            <span>Upload</span>
          </div>
        </div>
      </div>

      <div
        style={{
          display: "flex",
          marginLeft: "10px",
          marginRight: "10px",
          flexWrap: "nowrap",
          alignItems: "flex-start",
        }}
      >
        <div
          className={"fieldset"}
          style={{
            width: "100px",
            whiteSpace: "nowrap",
            flexWrap: "nowrap",
            display: "flex",
            alignItems: "flex-start",
          }}
        >
          <div
            className="label"
            style={{ whiteSpace: "nowrap" }}
          >{`Scale: ${scale}`}</div>
        </div>
        <div style={{ width: "100px", marginLeft: "20px", marginTop: "13px" }}>
          <SliderWithTooltip
            max={16}
            min={0.2}
            step={0.1}
            value={scale}
            onChange={value => onChange_scale(value)}
            defaultValue={1}
            // marks={{
            //   0.2: "0.2",
            //   4: "4",
            //   8: "8",
            // }}
          />
        </div>
      </div>

      <BackgroundPosition
        pointX={position.x}
        onChange_pointX={onChange_pointX}
        pointY={position.y}
        onChange_pointY={onChange_pointY}
      />

      <div
        style={{
          display: "flex",
          marginLeft: "10px",
          marginRight: "10px",
          flexWrap: "nowrap",
          alignItems: "flex-start",
        }}
      >
        <div className={"fieldset"} style={{ width: "85px" }}>
          <div
            className="label"
            style={{ flexWrap: "nowrap", whiteSpace: "nowrap" }}
          >
            Opacity: {opacity}
          </div>
        </div>
        <div style={{ width: "80px", marginLeft: "10px", marginTop: "13px" }}>
          <SliderWithTooltip
            max={1}
            min={0}
            step={0.1}
            value={opacity}
            onChange={value => onChange_opacity(value)}
            defaultValue={1}
          />
        </div>
      </div>
    </div>
  );
};

const BackgroundPosition = ({
  pointX,
  onChange_pointX,
  pointY,
  onChange_pointY,
}) => {
  return (
    <div
      style={{
        display: "flex",
        flexWrap: "nowrap",
        alignItems: "center",
        marginRight: "15px",
      }}
    >
      <div className={"fieldset"}>
        <div className="label">Image position: </div>

        <NumberFormat
          allowNegative={false}
          allowEmptyFormatting={true}
          max={999}
          min={-999}
          maxLength={3}
          value={pointX}
          onValueChange={event => {
            onChange_pointX(event.floatValue);
          }}
          placeholder="x"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "0px 10px",
            marginBottom: "0px",
            marginTop: "0px",
            maxWidth: "70px",
            marginRight: "5px",
          }}
        />
        <NumberFormat
          allowNegative={false}
          allowEmptyFormatting={true}
          max={999}
          min={-999}
          maxLength={3}
          value={pointY}
          onValueChange={event => {
            onChange_pointY(event.floatValue);
          }}
          placeholder="y"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "0px 10px",
            marginBottom: "0px",
            marginTop: "0px",
            maxWidth: "70px",
          }}
        />
      </div>
    </div>
  );
};

const SubMenuGrid = () => {
  const dispatch = useDispatch();
  const grid = useSelector((state: Editor) => {
    return state.ui.grid;
  });

  const { height, width, hexSize, type, snapEnabled, isVisible } = grid;
  const onChange_isVisible = () => {
    dispatch(Actions.Topbar.action_toggleGridIsVisible());
  };
  const onChange_snapEnabled = () => {
    dispatch(Actions.Topbar.action_toggleGridSnapEnabled());
  };
  const onChange_gridItemW = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridWidth(newValue));
  };
  const onChange_gridItemH = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridHeight(newValue));
  };
  const onClick_selectTypeHex = () => {
    dispatch(Actions.Topbar.action_setGridType(EditorGridType.Hex));
  };
  const onClick_selectTypeBox = () => {
    dispatch(Actions.Topbar.action_setGridType(EditorGridType.Box));
  };
  const onChange_hexSize = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridHexSize(newValue));
  };

  return (
    <div style={{ display: "flex", flexWrap: "nowrap", alignItems: "center" }}>
      <GridOptions
        isVisible={isVisible}
        onChange_isVisible={onChange_isVisible}
        snapEnabled={snapEnabled}
        onChange_snapEnabled={onChange_snapEnabled}
        type={type}
        onClick_selectTypeHex={onClick_selectTypeHex}
        onClick_selectTypeBox={onClick_selectTypeBox}
      />

      {type === EditorGridType.Box ? (
        <GridBoxSettings
          gridItemW={width}
          onChange_gridItemW={onChange_gridItemW}
          gridItemH={height}
          onChange_gridItemH={onChange_gridItemH}
        />
      ) : null}

      {type === EditorGridType.Hex ? (
        <GridHexSettings
          hexSize={hexSize}
          onChange_hexSize={onChange_hexSize}
        />
      ) : null}
    </div>
  );
};

const GridOptions = ({
  isVisible,
  onChange_isVisible,
  snapEnabled,
  onChange_snapEnabled,
  type,
  onClick_selectTypeHex,
  onClick_selectTypeBox,
}) => {
  return (
    <React.Fragment>
      <div
        style={{
          marginRight: "15px",
        }}
      >
        <FieldCheckbox
          label={"Show grid"}
          value={isVisible}
          onChange={onChange_isVisible}
          isDisabled={false}
        />
      </div>

      <div
        style={{
          marginRight: "15px",
        }}
      >
        <FieldCheckbox
          label={"Snap to grid"}
          value={snapEnabled}
          onChange={onChange_snapEnabled}
          isDisabled={false}
        />
      </div>

      <div
        style={{
          marginRight: "15px",
        }}
      >
        <div className={"fieldset"}>
          <div className="label">Grid type: </div>
          <div className="control">
            <div
              className={` button is-light ${
                type === EditorGridType.Hex ? "is-dark" : ""
              } `}
              onClick={onClick_selectTypeHex}
            >
              <span>Hexagon</span>
            </div>
          </div>

          <div className="control">
            <div
              className={` button is-light ${
                type === EditorGridType.Box ? "is-dark" : ""
              }`}
              onClick={onClick_selectTypeBox}
            >
              <span>Rectangle</span>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

