import { ImmerReducer } from "immer-reducer";
import { Editor, EditorMode, Point } from "../editorObjects/Editor";
import {
  buildPoint,
  getDistanceBetweenPoints,
  getRotate,
  toDeg,
} from "../editorObjects/Point";
import { buildBead } from "../editorObjects/Bead";
import { buildLine } from "../editorObjects/Line";
import { getPointFromStartLine } from "../editorObjects/Line";
import { merge } from "lodash";

export class BeadSnapperImmerReducer extends ImmerReducer<Editor> {
  
  // useHexGrid(){
  //   xPos = P.transform.position[0];
  //   yPos = P.transform.position[1];

  //   cSize = L.effect("Cell Size")("Slider");
  //   xOffset = L.effect("X Offset")("Slider");
  //   yOffset = L.effect("Y Offset")("Slider");

  //   // Convert size value into distance values between cells
  //   cWidth = cSize*.75;
  //   cHeight = cSize*13/15;

  //   // Find new X and Y positions
  //   yNew = yPos - yPos % cHeight + cHeight/2;
  //   xNew = xPos - (xPos % cWidth) + (((yNew - cHeight/2).toFixed(2)/cHeight).toFixed(2) % 1) * (cWidth/2);

  //   // Check for even/odd column, adjust properly
  //   if (!(xNew % (cWidth*2) == 0)) yNew += cHeight/2;

  //   // Implement offset
  //   [xNew + xOffset, yNew+yOffset]
  // }

  updateBeadsPositions(lineId: string) {
    // console.log("updateBeadsPositions: ---------------------- start");
    const currentLine = this.draftState.appData.lines[lineId];
    const count = this.getLineBeadsCount(lineId);
    const currentCount = currentLine.beadIds.length;
    // console.log(
    //   "updateBeadsPositions: currentCount: ",
    //   currentCount,
    //   " count",
    //   count
    // );
    if (count > currentCount) {
      const remaining = count - currentCount;
      this.addRemainingBeads(lineId, remaining);
    } else if (count < currentCount) {
      const overflow = currentCount - count;
      this.removeOverflowBeads(lineId, overflow);
    }
    this.draftState.appData.lines[lineId].settings.beadsCount = count;
    this.modifyBeadsPosition(lineId);
    this.modifyBeadsRotate(lineId);
    // console.log("updateBeadsPositions: ----------------------  end");
  }

  getLineBeadsCount(lineId) {
    const currentLine = this.draftState.appData.lines[lineId];
    const {
      beadsAutoCount,
      beadsAutoSpacing,
      beadsCount,
      beadsSpacing,
      defaultBead,
    } = currentLine.settings;
    const { start, end, beadIds } = currentLine;
    const lineLen = getDistanceBetweenPoints(start, end);

    // console.log(
    //   "getLineBeadsCount ",
    //   lineLen,
    //   beadsAutoCount === true,
    //   beadsAutoSpacing === true
    // );
    if (beadsAutoCount === true && beadsAutoSpacing === true) {
      // beadsy sa przyklejone do siebie. COunt powinien byc tyle ile sie zmiesci na lini
      let beadsWidths: number[] = [];
      let beadsWidth = 0;
      beadIds.forEach(beadId => {
        const width = this.draftState.appData.beads[beadId].width;
        beadsWidth = beadsWidth + width;
        beadsWidths.push(width);
      });
      if (lineLen >= beadsWidth) {
        // ile defaultsow trzeba dodac aby linia byla wypelniona?
        const defaultBeadWidth = defaultBead.width ? defaultBead.width : 10;
        const remainingSpace = lineLen - beadsWidth;
        const count =
          Math.floor(remainingSpace / defaultBeadWidth) + beadsWidths.length;

        return count;
      } else {
        // trzeba odejmowac elementy z lini do momentu az wszystkie sie zmnieszcza
        for (let i = beadsWidths.length - 1; i >= 0; i--) {
          beadsWidth = beadsWidth - beadsWidths[i];
          if (lineLen > beadsWidth) {
            const count = i;
            return count;
          }
        }
        // nie powinno byc elementow na linie, bo nie ma miejsca z lineLen
        return 0;
      }
    } else if (beadsAutoCount === false && beadsAutoSpacing === false) {
      return beadsCount;
    } else if (beadsAutoCount === true && beadsAutoSpacing === false) {
      let beadsWidths: number[] = [];
      let beadsWidth = 0;
      beadIds.forEach(beadId => {
        const width =
          this.draftState.appData.beads[beadId].width + beadsSpacing;
        beadsWidth = beadsWidth + width;
        beadsWidths.push(width);
      });
      if (lineLen >= beadsWidth - beadsSpacing) {
        const defaultBeadWidth = defaultBead.width ? defaultBead.width : 10;
        const remainingSpace = lineLen - (beadsWidth - beadsSpacing);
        const count =
          Math.floor(remainingSpace / (defaultBeadWidth + beadsSpacing)) +
          beadsWidths.length;
        return count;
      } else {
        for (let i = beadsWidths.length - 1; i >= 0; i--) {
          beadsWidth = beadsWidth - beadsWidths[i];
          if (lineLen > beadsWidth - beadsSpacing) {
            const count = i;
            return count;
          }
        }
        return 0;
      }
    } else if (beadsAutoCount === false && beadsAutoSpacing === true) {
      return beadsCount;
    }
    return 0;
  }

  addRemainingBeads(lineId, remainingCount) {
    const currentLine = this.draftState.appData.lines[lineId];
    for (let i = 0; i < remainingCount; i++) {
      let defaultBead: any = {};
      if (currentLine.settings.defaultBead) {
        defaultBead = { ...currentLine.settings.defaultBead };
      } else if (
        this.draftState.ui.lineSettings.settings &&
        this.draftState.ui.lineSettings.settings.defaultBead
      ) {
        defaultBead = {
          ...this.draftState.ui.lineSettings.settings.defaultBead,
        };
      }
      defaultBead.id = undefined;
      defaultBead.position = undefined;
      const bead = buildBead(defaultBead);
      this.draftState.appData.beads[bead.id] = bead;
      this.draftState.appData.lines[lineId].beadIds.push(bead.id);
    }
  }

  removeOverflowBeads(lineId, overflowCount) {
    for (let i = 0; i < overflowCount; i++) {
      const arrLen = this.draftState.appData.lines[lineId].beadIds.length;
      const lastItemBeadId = this.draftState.appData.lines[
        lineId
      ].beadIds.splice(arrLen - 1, 1);
      delete this.draftState.appData.beads[lastItemBeadId[0]];
    }
  }

  modifyBeadsPosition(lineId) {
    const currentLine = this.draftState.appData.lines[lineId];
    const { start, end, beadIds } = currentLine;
    const {
      beadsAutoCount,
      beadsAutoSpacing,
      beadsCount,
      beadsSpacing,
      defaultBead,
    } = currentLine.settings;

    const standardLineHeight = 10;

    if (beadsAutoCount === true && beadsAutoSpacing === true) {
      let currentDistance = 0;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        const { width, height } = this.draftState.appData.beads[beadId];
        currentDistance = currentDistance + width / 2;
        const newPosition = getPointFromStartLine(
          start.x,
          start.y,
          end.x,
          end.y,
          currentDistance
        );
        const x = newPosition.x - width / 2;
        const y = newPosition.y - height / 2 + standardLineHeight / 2;
        this.draftState.appData.beads[beadId].position.x = x;
        this.draftState.appData.beads[beadId].position.y = y;
        currentDistance = currentDistance + width / 2;
      });
    } else if (beadsAutoCount === false && beadsAutoSpacing === false) {
      let currentDistance = 0;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        const { width, height } = this.draftState.appData.beads[beadId];
        currentDistance = currentDistance + width / 2;
        const newPosition = getPointFromStartLine(
          start.x,
          start.y,
          end.x,
          end.y,
          currentDistance
        );
        const x = newPosition.x - width / 2;
        const y = newPosition.y - height / 2 + standardLineHeight / 2;
        this.draftState.appData.beads[beadId].position.x = x;
        this.draftState.appData.beads[beadId].position.y = y;
        currentDistance = currentDistance + width / 2 + beadsSpacing;
      });
    } else if (beadsAutoCount === true && beadsAutoSpacing === false) {
      let currentDistance = 0;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        const { width, height } = this.draftState.appData.beads[beadId];

        currentDistance = currentDistance + width / 2;
        const newPosition = getPointFromStartLine(
          start.x,
          start.y,
          end.x,
          end.y,
          currentDistance
        );
        const x = newPosition.x - width / 2;
        const y = newPosition.y - height / 2 + standardLineHeight / 2;
        this.draftState.appData.beads[beadId].position.x = x;
        this.draftState.appData.beads[beadId].position.y = y;
        currentDistance = currentDistance + width / 2 + beadsSpacing;
      });
    } else if (beadsAutoCount === false && beadsAutoSpacing === true) {
      let beadsWidth = 0;
      beadIds.forEach(beadId => {
        const width = this.draftState.appData.beads[beadId].width;
        beadsWidth = beadsWidth + width;
      });
      const lineLen = getDistanceBetweenPoints(start, end);
      const avaiableSpace = lineLen - beadsWidth;
      let dynamicBeadsSpacing = 0;
      if (beadIds.length > 1) {
        dynamicBeadsSpacing = avaiableSpace / (beadIds.length - 1);
      }
      let currentDistance = 0;
      this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
        const { width, height } = this.draftState.appData.beads[beadId];
        currentDistance = currentDistance + width / 2;
        const newPosition = getPointFromStartLine(
          start.x,
          start.y,
          end.x,
          end.y,
          currentDistance
        );
        const x = newPosition.x - width / 2;
        const y = newPosition.y - height / 2 + standardLineHeight / 2;
        this.draftState.appData.beads[beadId].position.x = x;
        this.draftState.appData.beads[beadId].position.y = y;
        currentDistance = currentDistance + width / 2 + dynamicBeadsSpacing;
      });
    }
  }

  modifyBeadsRotate(lineId) {
    const currentLine = this.draftState.appData.lines[lineId];
    const { start, end, beadIds } = currentLine;
    const lineRotateRadian = getRotate(start.x - end.x, start.y - end.y);
    beadIds.forEach(beadId => {
      this.draftState.appData.beads[beadId].lineRotate =
        toDeg(lineRotateRadian) * -1;
    });
  }
}
