import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFileAlt,
  faMarker,
  faCog,
  faImage,
} from "@fortawesome/free-solid-svg-icons";
import { uiButton } from "../colors";
import { useDispatch, useSelector } from "react-redux";
import { Actions } from "../../store";
import { Editor, EditorMenuMain } from "../../editorObjects/Editor";

export const TopbarMain = () => {
  const dispatch = useDispatch();
  const currentMenuMainOpen = useSelector((state: Editor) => {
    return state.ui.currentMenuMainOpen;
  });
  const onClick_file = () => {
    dispatch(Actions.Topbar.action_toggleMenuFile());
  };
  const onClick_grid = () => {
    dispatch(Actions.Topbar.action_toggleMenuSettings());
  };
  const onClick_background = () => {
    dispatch(Actions.Topbar.action_toggleMenuBackground());
  };
  return (
    <div
      className="field has-addons"
      style={{ margin: 0, border: "1px solid #e0e0e0", paddingBottom: "0px" }}
    >
      <div className="control">
        <div
          className={`button is-light ${
            currentMenuMainOpen === EditorMenuMain.File ? "is-dark" : ""
          }`}
          onClick={onClick_file}
          style={{ height: "25px", padding: "3px 10px" }}
        >
          <span className="icon is-small">
            <FontAwesomeIcon icon={faFileAlt} color={uiButton} />
          </span>
          <span>File</span>
        </div>
      </div>

      {/* <div className="control">
        <div className={`button`} onClick={onClick_selection}>
          <span className="icon is-small">
            <FontAwesomeIcon icon={faMarker} />
          </span>
          <span>Canvas</span>
        </div>
      </div> */}

      <div className="control">
        <div
          className={`button is-light ${
            currentMenuMainOpen === EditorMenuMain.Grid ? "is-dark" : ""
          }`}
          onClick={onClick_grid}
          style={{ height: "25px", padding: "3px 10px" }}
        >
          <span className="icon is-small">
            <FontAwesomeIcon icon={faCog} color={uiButton} />
          </span>
          <span>Grid</span>
        </div>
      </div>

      <div className="control">
        <div
          className={`button is-light ${
            currentMenuMainOpen === EditorMenuMain.Background ? "is-dark" : ""
          }`}
          onClick={onClick_background}
          style={{ height: "25px", padding: "3px 10px" }}
        >
          <span className="icon is-small">
            <FontAwesomeIcon icon={faImage} color={uiButton} />
          </span>
          <span>Background</span>
        </div>
      </div>
    </div>
  );
};
