import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faSave, faDownload } from "@fortawesome/free-solid-svg-icons";
import { uiButton } from "../colors";
import { useDispatch, useSelector } from "react-redux";
import { Editor } from "../../editorObjects/Editor";
import { Actions } from "../../store";

export const EditorChangeState = () => {
  const dispatch = useDispatch();
  const isSaved = useSelector((state: Editor) => {
    return state.ui.saveInfo.saved;
  });
  const haveUnsavedChanges = useSelector((state: Editor) => {
    return state.ui.saveInfo.haveUnsavedChanges;
  });

  const onClearCanvas = () => {
    dispatch(Actions.Persist.action_onClearCanvas());
  };

  const readSavedCanvas = () => {
    dispatch(Actions.Persist.action_readSavedCanvas());
  };

  const onSaveCanvas = () => {
    dispatch(Actions.Persist.action_onSaveCanvas());
  };

  return (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <div
        className="field has-addons"
        style={{ width: "auto", flexWrap: "nowrap", marginBottom: "0" }}
      >
        <div className="control">
          <div className={` button is-light `} onClick={onClearCanvas}>
            <span className="icon is-small">
              <FontAwesomeIcon icon={faTimes} color={uiButton} />
            </span>
            <span>New project</span>
          </div>
        </div>

        <div className="control">
          <div
            className={` button is-light ${
              isSaved === true ? "is-success" : ""
            }`}
            onClick={onSaveCanvas}
          >
            <span className="icon is-small">
              <FontAwesomeIcon icon={faSave} color={uiButton} />
            </span>
            <span>Save</span>
          </div>
        </div>

        <div className="control">
          <div className={`button is-light `} onClick={readSavedCanvas}>
            <span className="icon is-small">
              <FontAwesomeIcon icon={faDownload} color={uiButton} />
            </span>
            <span>Load local</span>
          </div>
        </div>
      </div>
      <div
        style={{
          fontFamily: "Open Sans, sans-serif",
          fontSize: "13px",
          alignSelf: "center",
        }}
      >
        {haveUnsavedChanges === true ? (
          <div>You have unsaved changes</div>
        ) : (
          <div>No changes to save</div>
        )}
      </div>
    </div>
  );
};
