import React from "react";
import { getTranslate3dCss } from "./MapValuesToCss";
import { DndProvider } from "react-dnd";
import HTML5Backend from "react-dnd-html5-backend";
import { DraggableCore } from "react-draggable";
import { useSelector } from "react-redux";
import { Editor, EditorGridType } from "../../editorObjects/Editor";

export const DisplayGroupLines = ({ location, onDrag }) => {
  const { x, y, w, h } = location;

  const snapToGrid = useSelector((state: Editor) => {
    return state.ui.grid.snapEnabled;
  });
  const gridItemW = useSelector((state: Editor) => {
    return state.ui.grid.width;
  });
  const gridItemH = useSelector((state: Editor) => {
    return state.ui.grid.height;
  });

  const type = useSelector((state: Editor) => {
    return state.ui.grid.type;
  });

  return (
    <DndProvider backend={HTML5Backend}>
      <DraggableCore
        // onStart={onDrag}
        onDrag={onDrag}
        // onStop={onDrag}
        grid={
          snapToGrid === true && type === EditorGridType.Box
            ? [gridItemW, gridItemH]
            : undefined
        }
        scale={1}
        disabled={false}
      >
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: w,
            height: h,
            transform: getTranslate3dCss(x, y),
            opacity: 1,
            zIndex: 22,
            border: "1px dashed rgba(0,0,0,0.2)",
            background: "rgba(0,0,0,0.05)",
            cursor: "move",
          }}
        >
          &nbsp;
        </div>
      </DraggableCore>
    </DndProvider>
  );
};
