import { Editor, EditorMenuMain } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { buildPoint } from "../editorObjects/Point";

export class TopbarImmerReducer extends ImmerReducer<Editor> {
  action_toggleMenuFile() {
    if (this.draftState.ui.currentMenuMainOpen === EditorMenuMain.File) {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.None;
    } else {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.File;
    }
  }
  action_toggleMenuSettings() {
    if (this.draftState.ui.currentMenuMainOpen === EditorMenuMain.Grid) {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.None;
    } else {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.Grid;
    }
  }
  action_toggleMenuBackground() {
    if (this.draftState.ui.currentMenuMainOpen === EditorMenuMain.Background) {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.None;
    } else {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.Background;
    }
  }
  action_toggleGridSnapEnabled() {
    this.draftState.ui.grid.snapEnabled = !this.draftState.ui.grid.snapEnabled;
  }
  action_toggleGridIsVisible() {
    this.draftState.ui.grid.isVisible = !this.draftState.ui.grid.isVisible;
  }
  action_setGridWidth(value) {
    this.draftState.ui.grid.width = value;
  }
  action_setGridHeight(value) {
    this.draftState.ui.grid.height = value;
  }
  action_setGridType(value) {
    this.draftState.ui.grid.type = value;
  }
  action_setGridHexSize(value) {
    this.draftState.ui.grid.hexSize = value;
  }

  action_background_image_clear() {
    this.draftState.ui.background.image.position = buildPoint(0, 0);
    this.draftState.ui.background.image.scale = 1;
    this.draftState.ui.background.image.url = "";
  }
  action_background_image_upload(value) {
    this.draftState.ui.background.image.url = value;
  }
  action_background_image_pointX(value) {
    this.draftState.ui.background.image.position.x = value;
  }
  action_background_image_pointY(value) {
    this.draftState.ui.background.image.position.y = value;
  }
  action_background_image_scale(value) {
    this.draftState.ui.background.image.scale = value;
  }
  action_background_image_opacity(value) {
    this.draftState.ui.background.image.opacity = value;
  }
  
}
