import { immerable } from "immer";
import { DeepPartial } from "ts-essentials";

export interface Point {
  x: number;
  y: number;
  [immerable]: boolean;
}

export enum BeadTypes {
  BasicRectangle = "BasicRectangle",
  BasicEllipse = "BasicEllipse",
}

export interface Bead {
  id: string;
  position: Point;
  [immerable]: boolean;
  width: number;
  height: number;
  type: BeadTypes;
  color: string;
  rotate: number;
  lineRotate: number;
  keepAspectRatio: boolean;
}

export interface Line {
  id: string;
  start: Point;
  end: Point;
  beadIds: string[];
  [immerable]: boolean;
  settings: {
    beadsCount: number;
    beadsAutoCount: boolean;
    beadsSpacing: number;
    beadsAutoSpacing: boolean;
    defaultBead: Partial<Bead>;
  };
}

export interface AppData {
  beads: { [s: string]: Bead };
  lines: { [s: string]: Line };
}

export enum EditorMode {
  Idle = "Idle",
  EditLines = "EditLines",
  AddLine = "AddLine",
  EditBeads = "EditBeads",
  Paint = "ColorPaint",
}
export enum EditorMenuMain {
  None = "None",
  File = "File",
  Grid = "Grid",
  Background = "Background",
}
export enum EditorGridType {
  Hex = "Hex",
  Box = "Box",
}
export enum BrushType {
  Color = "Color",
}

export interface EditorUi {
  lineSettings: DeepPartial<Line>;
  saveInfo: {
    saved: boolean;
    haveUnsavedChanges: boolean;
  };
  selectedLinesIds: { [s: string]: boolean };
  selectedLinesIdsCount: number;
  selectedBeadsIds: { [s: string]: boolean };
  selectedBeadsIdsCount: number;
  mouseSelection: {
    start: Point;
    end: Point;
    isDragStarted: boolean;
    isDragged: boolean;
  };
  currentMenuMainOpen: EditorMenuMain;
  grid: {
    type: EditorGridType;
    hexSize: number;
    snapEnabled: boolean;
    isVisible: boolean;
    width: number;
    height: number;
  };
  brush: {
    type: BrushType;
    size: number;
    color: string;
  };
  background: {
    image: {
      url: string;
      scale: number;
      position: Point;
      opacity: number;
    };
  };
}

export interface Editor {
  appData: AppData;
  ui: EditorUi;
  mode: EditorMode;
}
