import { Editor, BrushType } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { getDistanceBetweenPoints, buildPoint } from "../editorObjects/Point";

export class PaintImmerReducer extends ImmerReducer<Editor> {
  action_applyColorBrush(x, y) {
    const { color, type, size } = this.draftState.ui.brush;
    if (type === BrushType.Color) {
      const mousePoint = buildPoint(x + size / 2, y + size /2);
      Object.keys(this.draftState.appData.beads).forEach(beadId => {
        const currBead = this.draftState.appData.beads[beadId];
        if (currBead.color !== color) {
          const centerPoint = buildPoint(
            currBead.position.x + currBead.width / 2,
            currBead.position.y + currBead.height / 2
          );
          const len = getDistanceBetweenPoints(centerPoint, mousePoint);
          const brush = size / 2 + Math.min(currBead.width , currBead.height) / 2;
          if (len < brush) {
            this.draftState.appData.beads[beadId].color = color;
          }
        }
      });
    }
  }
}
