import React, { useState } from "react";
import { getDistanceBetweenPoints, getRotate } from "../../editorObjects/Point";
import { getTranslate3dCss, getRotateCss } from "../operations/MapValuesToCss";
import { DraggableCore, DraggableData } from "react-draggable";
import { useDispatch, useSelector } from "react-redux";
import { Actions } from "../../store";
import { Editor, EditorGridType, Point } from "../../editorObjects/Editor";

export const SelectedStraightLine = ({ lineId, start, end }) => {
  const size = 0;
  const height = 10;
  const width = getDistanceBetweenPoints(start, end);
  const rotate = getRotate(end.y - start.y, end.x - start.x);

  const dispatch = useDispatch();

  const snapToGrid = useSelector((state: Editor) => {
    return state.ui.grid.snapEnabled;
  });
  const gridItemW = useSelector((state: Editor) => {
    return state.ui.grid.width;
  });
  const gridItemH = useSelector((state: Editor) => {
    return state.ui.grid.height;
  });

  const type = useSelector((state: Editor) => {
    return state.ui.grid.type;
  });

  const [startX, set_tempX] = useState(0);
  const [startY, set_tempY] = useState(0);

  const [lineStart, set_lineStart] = useState<Point | null>(null);
  const [lineEnd, set_lineEnd] = useState<Point | null>(null);

  const onStart = (_, { x, y }: DraggableData) => {
    console.log("rozpoczecie ", start, end, x, y);
    set_tempX(x);
    set_tempY(y);
    set_lineStart({ ...start });
    set_lineEnd({ ...end });
  };
  const onStop = (_, { x, y }: DraggableData) => {
    console.log("zakoneczenie ", start, end, x, y);
    set_tempX(0);
    set_tempY(0);
    set_lineStart(null);
    set_lineEnd(null);
  };
  const onDrag = (_, { x, y, deltaX, deltaY }: DraggableData) => {
    dispatch(
      Actions.Draggable.action_onLineDrag(lineId, {
        x,
        y,
        deltaX,
        deltaY,
        startX,
        startY,
        lineStart,
        lineEnd,
      })
    );
  };

  return (
    <DraggableCore
      onStart={onStart}
      onDrag={onDrag}
      onStop={onStop}
      grid={
        snapToGrid === true && type === EditorGridType.Box
          ? [gridItemW, gridItemH]
          : undefined
      }
      disabled={false}
      scale={1}
    >
      <div
        style={{
          height,
          width,
          backgroundColor: "rgba(0,0,0,0.05)",
          border: "1px dashed rgba(0,0,0,0.60)",
          transform: `${getTranslate3dCss(
            start.x + size,
            start.y + size
          )} ${getRotateCss(rotate)}`,
          transformOrigin: "left center",
          cursor: "move",
          zIndex: 21,
        }}
        className="line"
      >
        &nbsp;
      </div>
    </DraggableCore>
  );
};
