import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { Actions } from "../../../store";
import { FieldsetColor } from "../ui/FieldsetColor";
import { FieldsetSize } from "../ui/FieldsetSize";

export const ToolbarPaint = ({ isVisible }) => {
  if (isVisible === true) {
    return <Content />;
  }

  return <React.Fragment />;
};

const Content = () => {
  return (
    <React.Fragment>
      <BrushSize />
      <BrushColor />
    </React.Fragment>
  );
};

const BrushSize = () => {
  const dispatch = useDispatch();
  const size = useSelector((state: Editor) => {
    return state.ui.brush.size;
  });

  if (!size) {
    return <React.Fragment />;
  }

  const onChange = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(Actions.Toolbar.action_updateBrushSize(newValue));
  };

  return <FieldsetSize value={size} onChange={onChange} />;
};

const BrushColor = () => {
  const dispatch = useDispatch();
  const color = useSelector((state: Editor) => {
    return state.ui.brush.color;
  });

  if (!color) {
    return <React.Fragment />;
  }

  const onChange = color => {
    dispatch(Actions.Toolbar.action_updateBrushColor(color));
  };

  return <FieldsetColor onChange={onChange} value={color} />;
};
