import {
  Editor,
  EditorMode,
  Point,
  EditorGridType,
} from "../editorObjects/Editor";
import { buildPoint, getDistanceBetweenPoints } from "../editorObjects/Point";
import { ImmerReducer } from "immer-reducer";
import { buildBead } from "../editorObjects/Bead";
import { buildLine } from "../editorObjects/Line";
import { getPointFromStartLine } from "../editorObjects/Line";
import { BeadSnapperImmerReducer } from "./BeadSnapperImmerReducer";

const lineBoxOffset = 5;

export class DraggableOperationsImmerReducer extends BeadSnapperImmerReducer {
  editorAppDataHasChanged() {
    this.draftState.ui.saveInfo.saved = false;
    this.draftState.ui.saveInfo.haveUnsavedChanges = true;
  }

  action_onAddLine(start: Point, end: Point) {
    const line = buildLine({ ...this.draftState.ui.lineSettings, start, end });
    this.draftState.appData.lines[line.id] = line;
    this.draftState.mode = EditorMode.EditLines;
    this.updateBeadsPositions(line.id);
    this.editorAppDataHasChanged();
  }

  action_onStartPointDrag(
    key: string,
    data: {
      x: number;
      y: number;
    }
  ) {
    // console.log("action_onStartPointDrag");
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      const {
        width,
        height,
        snapEnabled,
        type,
        hexSize,
      } = this.draftState.ui.grid;
      if (snapEnabled === true) {
        if (type === EditorGridType.Box) {
          line.start.x = Math.round(data.x / width) * width - lineBoxOffset;
          line.start.y = Math.round(data.y / height) * height - lineBoxOffset;
        } else if (type === EditorGridType.Hex) {
          const hexWidth = Math.sqrt(3) * hexSize;
          const hSpace = hexWidth;
          const hexHeight = 2 * hexSize;
          const vSpace = hexHeight * (3 / 4);
          const changeX = Math.round(data.x / hSpace) * hSpace - lineBoxOffset;
          const vLine = Math.round(data.y / vSpace);
          const changeY = vLine * vSpace - lineBoxOffset - hexHeight / 4;
          line.start.y = changeY;
          if (vLine % 2 === 0) {
            line.start.x = hSpace / 2 + changeX;
          } else {
            line.start.x = changeX;
          }
        }
      } else {
        line.start.x = data.x - lineBoxOffset;
        line.start.y = data.y - lineBoxOffset;
      }
      this.updateBeadsPositions(line.id);
      this.editorAppDataHasChanged();
    }
  }

  action_onEndPointDrag(
    key: string,
    data: {
      x: number;
      y: number;
    }
  ) {
    // console.log("action_onEndPointDrag");
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      const {
        width,
        height,
        snapEnabled,
        hexSize,
        type,
      } = this.draftState.ui.grid;
      if (snapEnabled === true) {
        if (type === EditorGridType.Box) {
          line.end.x = Math.round(data.x / width) * width - lineBoxOffset;
          line.end.y = Math.round(data.y / height) * height - lineBoxOffset;
        } else if (type === EditorGridType.Hex) {
          const hexWidth = Math.sqrt(3) * hexSize;
          const hSpace = hexWidth;
          const hexHeight = 2 * hexSize;
          const vSpace = hexHeight * (3 / 4);
          const changeX = Math.round(data.x / hSpace) * hSpace - lineBoxOffset;
          const vLine = Math.round(data.y / vSpace);
          const changeY = vLine * vSpace - lineBoxOffset - hexHeight / 4;
          line.end.y = changeY;
          if (vLine % 2 === 0) {
            line.end.x = hSpace / 2 + changeX;
          } else {
            line.end.x = changeX;
          }
        }
      } else {
        line.end.x = data.x - lineBoxOffset;
        line.end.y = data.y - lineBoxOffset;
      }
      this.updateBeadsPositions(line.id);
      this.editorAppDataHasChanged();
    }
  }

  action_onLineDrag(
    key: string,
    data: {
      x: number;
      y: number;
      deltaX: number;
      deltaY: number;
      startX: number;
      startY: number;
      lineStart: Point | null;
      lineEnd: Point | null;
    }
  ) {
    const { startX, startY, lineStart, lineEnd, x, y } = data;
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      const {
        width,
        height,
        snapEnabled,
        type,
        hexSize,
      } = this.draftState.ui.grid;

      if (snapEnabled === true) {
        if (type === EditorGridType.Box) {
          line.start.x =
            Math.round((line.start.x + data.deltaX) / width) * width -
            lineBoxOffset;

          line.start.y =
            Math.round((line.start.y + data.deltaY) / height) * height -
            lineBoxOffset;

          line.end.x =
            Math.round((line.end.x + data.deltaX) / width) * width -
            lineBoxOffset;

          line.end.y =
            Math.round((line.end.y + data.deltaY) / height) * height -
            lineBoxOffset;
        } else if (type === EditorGridType.Hex) {
          const hexWidth = Math.sqrt(3) * hexSize;
          const hSpace = hexWidth;
          const hexHeight = 2 * hexSize;
          const vSpace = hexHeight * (3 / 4);

          if (lineStart !== null) {
            const newX = lineStart.x + (x - startX);
            const newY = lineStart.y + (y - startY);
            const changeX = Math.round(newX / hSpace) * hSpace - lineBoxOffset;

            const vLine = Math.round(newY / vSpace);
            const newChangeY = vLine * vSpace;
            const temp = hexHeight / 4;
            let changeY = newChangeY - lineBoxOffset - temp;
            line.start.y = changeY;
            if (vLine % 2 === 0) {
              line.start.x = hSpace / 2 + changeX;
            } else {
              line.start.x = changeX;
            }
          }

          if (lineEnd !== null) {
            const newX = lineEnd.x + (x - startX);
            const newY = lineEnd.y + (y - startY);
            const changeX = Math.round(newX / hSpace) * hSpace - lineBoxOffset;

            const vLine = Math.round(newY / vSpace);
            const newChangeY = vLine * vSpace;
            const temp = hexHeight / 4;
            let changeY = newChangeY - lineBoxOffset - temp;
            line.end.y = changeY;
            if (vLine % 2 === 0) {
              line.end.x = hSpace / 2 + changeX;
            } else {
              line.end.x = changeX;
            }
          }
        }
      } else {
        line.start.x = line.start.x + data.deltaX;
        line.start.y = line.start.y + data.deltaY;
        line.end.x = line.end.x + data.deltaX;
        line.end.y = line.end.y + data.deltaY;
      }
      this.updateBeadsPositions(line.id);
      this.editorAppDataHasChanged();
    }
  }

  action_onGroupLineDrag(data: { deltaX: number; deltaY: number }) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      if (this.draftState.appData.lines[lineId]) {
        const line = this.draftState.appData.lines[lineId];
        const { width, height, snapEnabled } = this.draftState.ui.grid;
        if (snapEnabled === true) {
          line.start.x =
            Math.round((line.start.x + data.deltaX) / width) * width -
            lineBoxOffset;

          line.start.y =
            Math.round((line.start.y + data.deltaY) / height) * height -
            lineBoxOffset;

          line.end.x =
            Math.round((line.end.x + data.deltaX) / width) * width -
            lineBoxOffset;

          line.end.y =
            Math.round((line.end.y + data.deltaY) / height) * height -
            lineBoxOffset;
        } else {
          line.start.x = line.start.x + data.deltaX;
          line.start.y = line.start.y + data.deltaY;
          line.end.x = line.end.x + data.deltaX;
          line.end.y = line.end.y + data.deltaY;
        }
        this.updateBeadsPositions(line.id);
      }
    });
    this.editorAppDataHasChanged();
  }

  action_onGroupBeadsDrag(data: { deltaX: number; deltaY: number }) {}

  action_onClickLine(lineId: string) {
    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIds[lineId] = true;
    this.draftState.ui.selectedLinesIdsCount = 1;
  }

  action_onClickLastestLine() {
    const keys = Object.keys(this.draftState.appData.lines);
    if (keys.length > 0) {
      const lineId = keys[keys.length - 1];
      this.action_onClickLine(lineId);
    }
  }

  action_onClickBead(beadId: string) {
    this.draftState.ui.selectedBeadsIds = {};
    this.draftState.ui.selectedBeadsIds[beadId] = true;
    this.draftState.ui.selectedBeadsIdsCount = 1;
  }
}
