import React from "react";
import { useSelector } from "react-redux";
import { Editor } from "../../editorObjects/Editor";

export const BackgroundImage = () => {
  const image = useSelector((state: Editor) => {
    return state.ui.background.image;
  });

  const { position, url, scale, opacity } = image;

  if (!url) {
    return <React.Fragment />;
  }

  return (
    <img
      src={url}
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        // width: "100%",
        // height: "100%",
        zIndex: 2,
        transform: `translate3d(${position.x}px, ${position.y}px, 1px) scale(${scale})`,
        transformOrigin: "top left",
        opacity,
      }}
    />
  );
};
