import React from "react";
import { range } from "lodash";
import { getTranslate3dCss } from "./MapValuesToCss";
import { useSelector } from "react-redux";
import { Editor, EditorGridType } from "../../editorObjects/Editor";
import { buildPoint } from "../../editorObjects/Point";
export const DisplayGridLines = ({ isVisible }) => {
  if (isVisible === false) {
    return <React.Fragment />;
  }

  return <Content />;
};

const Content = () => {
  const grid = useSelector((state: Editor) => {
    return state.ui.grid;
  });

  const { height, width, hexSize, type, isVisible } = grid;

  if (isVisible === false) {
    return <React.Fragment />;
  }

  if (type === EditorGridType.Box) {
    return <GridBox height={height} width={width} />;
  }

  if (type === EditorGridType.Hex) {
    return <GridHex hexSize={hexSize} />;
  }

  return <React.Fragment />;
};

const GridBox = ({ height, width }) => {
  const gridItemW = width;
  const gridItemH = height;
  const gridItemColor = "rgba(0,0,0,0.08)";
  const gridItemStrokeWidth = 1;
  const svg =
    `<svg xmlns='http://www.w3.org/2000/svg' width='${gridItemW}' height='${gridItemH}'>` +
    `  <line x1='${gridItemW / 2}' y1='0' x2='${gridItemW /
      2}' y2='${gridItemH}' style='stroke:${gridItemColor};stroke-width:${gridItemStrokeWidth}' />` +
    `  <line x1='0' y1='${gridItemH / 2}' x2='${gridItemW}' y2='${gridItemH /
      2}' style='stroke:${gridItemColor};stroke-width:${gridItemStrokeWidth}' />` +
    "</svg>";
  const backgroundUrl = `url("data:image/svg+xml;utf8,${svg}")`;
  return (
    <div
      style={{
        height: "100%",
        width: "100%",
        position: "absolute",
        top: 0,
        left: 0,
        backgroundImage: backgroundUrl,
        backgroundRepeat: "repeat",
        backgroundPosition: `${gridItemW / 2}px ${gridItemH / 2}px`,
        zIndex: 4,
      }}
    />
  );
};

const pointy_hex_corner = (center, size, i) => {
  var angle_deg = 60 * i - 30;
  var angle_rad = (Math.PI / 180) * angle_deg;
  return buildPoint(
    center.x + size * Math.cos(angle_rad),
    center.y + size * Math.sin(angle_rad)
  );

  // w = sqrt(3) * size
  // h = 2 * size
};

const GridHex = ({ hexSize }) => {
  const gridItemColor = "rgba(0,0,0,0.1)";
  const gridItemStrokeWidth = 1;

  const size = hexSize;
  const width = Math.sqrt(3) * size;
  const height = 2 * size;
  const startPoint = buildPoint(0, size);
  const movedPoint = buildPoint(startPoint.x, startPoint.y);
  movedPoint.x = movedPoint.x + width;
  const moreMovedPoint = buildPoint(startPoint.x, startPoint.y);
  moreMovedPoint.x = moreMovedPoint.x + width / 2;
  moreMovedPoint.y = moreMovedPoint.y + height * (3 / 4);
  const evenmoreMovedPoint = buildPoint(startPoint.x, startPoint.y);
  evenmoreMovedPoint.x = evenmoreMovedPoint.x - width / 2;
  evenmoreMovedPoint.y = evenmoreMovedPoint.y + height * (3 / 4);
  const lines = [
    [
      pointy_hex_corner(startPoint, size, 5),
      pointy_hex_corner(startPoint, size, 6),
    ],
    [
      pointy_hex_corner(startPoint, size, 0),
      pointy_hex_corner(startPoint, size, 1),
    ],
    [
      pointy_hex_corner(startPoint, size, 1),
      pointy_hex_corner(startPoint, size, 2),
    ],
    [
      pointy_hex_corner(movedPoint, size, 4),
      pointy_hex_corner(movedPoint, size, 5),
    ],
    [
      pointy_hex_corner(movedPoint, size, 2),
      pointy_hex_corner(movedPoint, size, 3),
    ],
    [
      pointy_hex_corner(movedPoint, size, 2),
      pointy_hex_corner(movedPoint, size, 3),
    ],
    [
      pointy_hex_corner(moreMovedPoint, size, 0),
      pointy_hex_corner(moreMovedPoint, size, 1),
    ],
    [
      pointy_hex_corner(evenmoreMovedPoint, size, 0),
      pointy_hex_corner(evenmoreMovedPoint, size, 1),
    ],
  ];

  const boxW = width;
  const boxH = 1.5 * height;

  let svg = `<svg xmlns='http://www.w3.org/2000/svg' width='${boxW}' height='${boxH}'>`;
  lines.forEach(points => {
    const [a, b] = points;
    const line =
      `<line x1='${a.x}' y1='${a.y}' x2='${b.x}' y2='${b.y}' ` +
      `style='stroke:${gridItemColor};stroke-width:${gridItemStrokeWidth}' />`;
    svg = svg + line;
  });
  svg = svg + "</svg>";

  const backgroundUrl = `url("data:image/svg+xml;utf8,${svg}")`;

  return (
    <div
      style={{
        height: "100%",
        width: "100%",
        position: "absolute",
        top: 0,
        left: 0,
        backgroundImage: backgroundUrl,
        backgroundRepeat: "repeat",
        backgroundPosition: `1px 0px`,
        zIndex: 4,
      }}
    ></div>
  );
};
